
// import { HashRouter, Route, Switch , Link } from "react-router-dom";
import { BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom';

import Home from "../src/component/home"
import faq from "../src/component/faq"
import blogs from "../src/component/blogs"
import blog1 from "../src/component/blog1"
import blog2 from "../src/component/blog2"
import blog3 from "../src/component/blog3"
import Terms from "../src/component/terms"
import Privacy from "../src/component/privacy"
import Bynumbers from "../src/component/bynumbers"
import learnmore from "../src/component/learnmore"
import acceptableusepolicy from "../src/component/wshare/acceptableusepolicy"
import ssa from "../src/component/wshare/ssa"
import wsl from "../src/component/wshare/wsl"
import './App.css';

function App() {
  return (
    <Router>
    <div className="App">
 
       
        <Switch>
        <Route exact path= "/" render={() => (
                  <Redirect to="/home"/>
                )}/>
          <Route exact path="/home" component={Home} />
          <Route exact path="/faq" component={faq} />
          <Route exact path="/blogs" component={blogs} />
          <Route exact path="/blog1" component={blog1} />
          <Route exact path="/blog2" component={blog2} />
          <Route exact path="/blog3" component={blog3} />
          <Route exact path="/terms" component={Terms} />
          <Route exact path="/privacy" component={Privacy} />
          <Route exact path="/bynumbers" component={Bynumbers} />
          <Route exact path="/bynumbers" component={Bynumbers} />
          <Route exact path="/ssa" component={ssa} />
          <Route exact path="/acceptableusepolicy" component={acceptableusepolicy} />
          <Route exact path="/wsl" component={wsl} />
          <Route exact path="/learnmore" component={learnmore} />
        </Switch>

    </div>
    </Router>
  );
}

export default App;
